import React from "react"
import styled from "styled-components"
import { Link } from "react-router-dom"
import { FaSearch, FaCartPlus, FaFileExcel } from "react-icons/fa"
import { ProductConsumer } from "../context"
import PersianNumber from '../components/PersianNumber'


export default function Product({ product }) {
  return (
    <ProductConsumer>
      {(value) => {
        const { addToCart, setSingleProduct } = value

        return (
          <ProductWrapper className="col-10 mx-auto col-sm-8 col-md-6 col-lg-4 col-xl-3 my-4 px-4">
            <div className="card">
              <div className="img-container">
                <img
                  src={product.image}
                  className="card-img-top p-5"
                  alt="product"
                  style={{ width: "250px"}}
                />
                
              </div>
              <div
                className="card-body d-flex justify-content-between"
                style={{ color: "RGB(14, 28, 47)", fontWeight: "300px" }}
              >
                <div className="product-icons">
                  <Link
                    to={`/products/${product.id}`}
                    onClick={() => setSingleProduct(product.id)}
                  >
                    <FaSearch className="icon" />
                  </Link>

                  <FaCartPlus
                    className="icon"
                    onClick={() => addToCart(product.id)}
                  />
                </div>                
              </div>
              <div className="card-footer bg-transparent  d-flex justify-content-between">
                <p className="mb-0"><PersianNumber>{product.title}</PersianNumber></p>
                <PersianNumber className="mb-0 font-weight-bold">{product.price} تومان </PersianNumber>
              </div>              
            </div>
           
          </ProductWrapper>
        )
      }}
    </ProductConsumer>
  )
}
// style={{ height: "320px" }}
const ProductWrapper = styled.div`
  .card {
    transition: var(--mainTransition);
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.31);
    height: 100%; 
    /* width:300px    */
  }
  .card:hover {
    box-shadow: 7px 10px 5px 0px rgba(0, 0, 0, 0.51);
    cursor: pointer;
  }
  .card-img-top {
    transition: var(--mainTransition);
  }
  .card:hover .card-img-top {
    transform: scale(1.15);
    /* opacity: 0.2; */
  }
  .img-container {
    position: relative;
  }
  .product-icons {
    transition: var(--mainTransition);
    position: absolute;
     top: 75%;    
    left: 50%; 
    transform: translate(-50%, -50%);
    opacity: 0;
    .icon {
      font-size: 2.7rem;
      margin: 0.5rem;
      padding: 0.5rem;
      color: #ff334f;
      background: transparent;
      /* background: #d7392c; */
      border-radius: 0.5rem;
    }
  }
  .card:hover .product-icons {
    opacity: 1;
  }
  .card-body {
    transition: var(--mainTransition);
    font-weight: bold;
    text-transform: uppercase;
  }

  .card-footer{
    border-color:#4dbd28;
    color:#070d59;
    font-weight:bold;
    
  }

  

  @media (max-width: 576px) {
    .card:hover .product-icons {
      opacity: 1;
    }
   
  }
`
