import React from "react"
import styled from "styled-components"
import { ProductConsumer } from "../../context"
import PersinNumber from "../PersianNumber"

const ProductFilter = () => {
  return (
    <ProductConsumer>
      {(value) => {
        const {
          search,
          min,
          max,
          company,
          price,
          shipping,
          handleChange,
          storeProducts,
        } = value

        {
          /* cheak for the company */
        }
        let companies = new Set()
        companies.add("همه")
        for (let product in storeProducts) {
          companies.add(storeProducts[product]["company"])
        }
        companies = [...companies]
        {
          /* end of cheak for company */
        }
        return (
          <div className="row my-5">
            <div className="col-10 mx-auto">
              <FilterWrapper>
                {/* text search */}
                <div>
                  <label htmlFor="search">جستجوی محصولات</label>
                  <input
                    type="text"
                    name="search"
                    id="search"
                    onChange={handleChange}
                    value={search}
                    className="filter-item"
                  />
                </div>
                {/* end of text search */}

                {/* category search */}
                <div>
                  <label htmlFor="company">دسته بندی کالا</label>
                  <select
                    name="company"
                    className="filter-item"
                    id="company"
                    value={company}
                    onChange={handleChange}
                  >
                    {companies.map((company, index) => {
                      return (
                        <option key={index} value={company}>
                          {company}
                        </option>
                      )
                    })}
                  </select>
                </div>
                {/* end of category search */}
                {/* price range */}
                <div>
                  <label htmlFor="price">
                    <p className="mb-2">
                      قیمت محصولات :
                      <span>
                        {" "}
                        <PersinNumber>{price} تومان</PersinNumber>
                      </span>
                    </p>
                  </label>
                  <input
                    type="range"
                    name="price"
                    id="price"
                    className="filter-price"
                    min={min}
                    max={max}
                    value={price}
                    onChange={handleChange}
                  />
                </div>
                {/* price range */}
                {/* free shipping */}
                <div>
                  <label htmlFor="shipping" className="mx-2">
                    ارسال رایگان
                  </label>
                  <input
                    type="checkbox"
                    name="shipping"
                    id="shipping"
                    value={shipping && true}
                    onChange={handleChange}
                  />
                </div>
                {/* free shipping */}
              </FilterWrapper>
            </div>
          </div>
        )
      }}
    </ProductConsumer>
  )
}

const FilterWrapper = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-column-gap: 2rem;
  grid-row-gap: 1rem;
  label {
    font-weight: bold;
    text-transform: capitalize;
  }
  .filter-item,
  .filter-price {
    display: block;
    width: 100%;
    background: transparent;
    border-radius: 0.5rem;
    border: 2px solid var(--darkGrey);
    
  }
`

export default ProductFilter
