import React from "react"
import { ProductConsumer } from "../context"
import styled from "styled-components"
import { Link } from "react-router-dom"
import PersionNumber from '../components/PersianNumber'

export default function SideCart() {
  return (
    <ProductConsumer>
      {(value) => {
        const { cartOpen, closeCart, cart, cartTotal } = value
        return (
          <CartWrapper show={cartOpen} onClick={closeCart}>
            <ul>
              {cart.map((item) => {
                return (
                  <li key={item.id} className="cart-item mb-4">
                    <img
                      width="70"
                      src={`../${item.image}`}
                      //src={item.image}
                      alt="cart item"
                    />
                    <div className="mt-3">
                      <h6
                        className="text-capitalize"
                        style={{ color: "#0e1c2e", fontSize: "1.5rem" }}
                      >
                       <PersionNumber>{item.title}</PersionNumber> 
                      </h6>
                      <h6
                        className="text-capitalize"
                        style={{ color: "#0e1c2e" }}
                      >
                        تعداد : <PersionNumber>{item.count}</PersionNumber>
                      </h6>
                    </div>
                  </li>
                )
              })}
            </ul>
            <h4 className="text-capitalize text-main">
              مبلغ قابل پرداخت : <PersionNumber>{cartTotal} تومان</PersionNumber>
            </h4>
            <div className="text-center my-5">
              <Link to="/cart" className="main-link">
                سبد خرید
              </Link>
            </div>
          </CartWrapper>
        )
      }}
    </ProductConsumer>
  )
}

const CartWrapper = styled.div`
  position: fixed;
  /* top: 56px; */
  top: 0px;
  /* right: 0; */
  left:0;
  width: 100%;
  height: 100%;
  background: var(--mainGrey);
  z-index: 1;
  border-right: 4px solid var(--mainWhite);
  transform: ${(props) => (props.show ? "translateX(0)" : "translateX(-100%)")};
  transition: var(--mainTransition);
  background: var(--mainGrey);

  .main-link{
    font-size:1.5rem;
    padding-top:0;
    padding-bottom:0
  }
  
  @media (min-width: 576px) {
    width: 20rem;
  }
  overflow: scroll;
  padding: 2rem;
  ul {
    padding: 0 !important;
  }
  .cart-item {
    list-style-type: none;
  }
`
