import React from "react"
import Hero from "../components/Hero"
import defultImage from "../images/defaultbg.jpg"
import { Link } from "react-router-dom"

const Default = () => {
  return (
    <Hero img={defultImage} title="404" max="true">
      <h2 className="text-uppercase">صفحه مورد نظر شما یافت نشد</h2>
      <Link to="/" className="main-link" style={{ marginTop: "2rem" }}>
        برگشت به خانه
      </Link>
    </Hero>
  );
}

export default Default
