import React from "react"
import Title from "../Title"
import lorem from 'falorem'
import aboutBcg from "../../images/bgabout.jpg"

export default function Info() {
  return (
    <section className="py-5">
      <div className="container">
        <div className="row">
          <div className="col-10 mx-auto col-md-6 my-3">
            <img
              src={aboutBcg}
              className="img-fluid img-thumbnail"
              alt="iphone"
              style={{ background: "var(--darkGrey)" }}
            />
          </div>
          <div className="col-10 mx-auto col-md-6 my-3 p-3">
            
            <Title title="درباره ما" center="true" />
            <p
              className="text-lead text-muted my-3"
              style={{ textAlign: "justify" }}
            >
              {lorem.sentences(2)}
            </p>
            <p
              className="text-lead text-muted my-3"
              style={{ textAlign: "justify" }}
            >
              {lorem.paragraphs(1)}
            </p>
            <button style={{ marginTop: "2rem" }} className="main-link">
              اطلاعات بیشتر
            </button>
          </div>
        </div>
      </div>
    </section>
  )
}
