import React from "react"
import { Link } from "react-router-dom"
import Hero from "../components/Hero"
import { ProductConsumer } from "../context"
import singleProductImg from "../images/fruitscollection.jpg"
import PersionNumber from '../components/PersianNumber'

const SingleProductPage = () => {
  return (
    <>
      <Hero img={singleProductImg} />
      <ProductConsumer>
        {(value) => {
          const { singleProduct, addToCart, loading } = value
          if (loading) {
            console.log("hello loading")

            return <h1>product loading...</h1>
          }

          const {
            company,
            description,
            id,
            price,
            title,
            image,
          } = singleProduct

          // console.log(url);
          return (
            <section className="py-5">
              <div className="container">
                <div className="row">
                  <div className="col-10 mx-auto col-sm-8 col-md-6 my-3">
                    <img
                      src={`../${image}`}
                      alt="single product"
                      className="img-fluid"
                    />
                  </div>

                  <div className=" col-10 mx-auto col-sm-8 col-md-6 my-3">
                    <h5 className="text-capitalize d-flex mb-4"><PersionNumber>{title}</PersionNumber></h5>
                    <h5 className="text-capitalize text-muted d-flex mb-4">
                     برند: {company}
                    </h5>
                    <h5 className="text-main text-capitalize d-flex mb-4 ">
                      قیمت : <PersionNumber>{price} تومان</PersionNumber>
                    </h5>
                    <p className="text-capitalize  mt-3 justify d-flex">
                      توضیحات محصول :
                    </p>
                    <p className="text-justify">{description}</p>

                    <button
                      type="button"
                      className="main-link text-capitalize"
                      style={{ margin: "0.75rem"}}
                      onClick={() => addToCart(id)}
                    >
                     افزودن به سبد خرید
                    </button>
                    <Link
                      to="/products"
                      className="main-link text-capitalize"
                      style={{ margin: "0.75rem" }}
                    >
                      برگشت به محصولات
                    </Link>
                  </div>
                </div>
              </div>
            </section>
          )
        }}
      </ProductConsumer>
    </>
  )
}

export default SingleProductPage
