import React from "react"
import styled from "styled-components"
import lorem from 'falorem'
import Credit from './image/credit-card.svg'
import Delivery from './image/free-delivery.svg'
import Return  from './image/return.svg'


class Services extends React.Component {
  state = {
    services: [
      {
        id: 1,
        icon: <img src={Delivery} alt="credit" style={{width:"40px"}}/>,
        title: lorem.words(4),
        text: lorem.sentences(1)
          
      },
      {
        id: 2,
        icon: <img src={Return} alt="credit" style={{width:"40px"}}/>,
        title: lorem.words(4),
        text:lorem.sentences(1)
      },
      {
        id: 3,
        icon: <img src={Credit} alt="credit" style={{width:"40px"}}/>,
        title: lorem.words(4),
        text: lorem.sentences(1)          
      },
    ],
  }
  render() {
    return (
      <ServicesWrapper>
        <div className="continer">
          <div className="row">
            {this.state.services.map((item) => {
              return (
                <div
                  className="col 10 mx-auto col-sm-6 col-md-4 text-center my-3"
                  key={item.id}
                >
                  <div className="service-icon">{item.icon}</div>
                  <div className="mt-3 text-capitalize">{item.title}</div>
                  <div className="mt-3">{item.text}</div>
                </div>
              )
            })}
          </div>
        </div>
      </ServicesWrapper>
    )
  }
}

const ServicesWrapper = styled.section`
  background: #ffffe6;
  .service-icon {
    font-size: 2.5rem;
    /* color: var(--primaryColor); */
    color: green;
    
  }
  p {
    color: #0e1c2f !important;
    /* var(--darkGrey); */
  }
`

export default Services
