import React from "react"
import { ProductConsumer } from "../../context"
import PayPalBtn from "./PayPalBtn"
import PersinNumber from '../PersianNumber'

const CartTotals = ({ history }) => {
  return (
    <div className="container">
      <div className="row">
        <ProductConsumer>
          {(value) => {
            const { clearCart, cartSubTotal, cartTax, cartTotal } = value
            console.log(cartTotal)

            return (
              <div className="col text-title text-center my-4" style={{font:"Calibri"}}>
                <button
                  className="btn btn-outline-danger text-capitalize mb-4"
                  onClick={clearCart}
                >
                  پاک کردن سبد 
                </button>
                
                <h5> جمع کل بدون اعمال مالیات : <PersinNumber>{cartSubTotal} تومان</PersinNumber></h5>
                <h5>مالیات : <PersinNumber>{cartTax} تومان</PersinNumber></h5>
                <h5>مجموع : <PersinNumber>{cartTotal} تومان</PersinNumber> </h5>

                {/* paypal button */}
                <PayPalBtn
                  history={history}
                  clearCart={clearCart}
                  cartTotal={cartTotal}
                />
              </div>
            )
          }}
        </ProductConsumer>
      </div>
    </div>
  )
}

export default CartTotals
