import React from "react"
import Title from "../Title"

const Contact = () => {
  return (
    <section className="py-5">
      <div className="row">
        <div className="col-10 mx-auto col-md-6 my-3">
          
          <Title title="تماس با ما" center="true" />
          <form className="mt-5">
            {/* first name */}
            <div className="form-group">
              <input
                type="text"
                name="firstName"
                className="form-control"
                placeholder="نام"
              />
            </div>
            {/* end first name */}

            {/* email */}
            <div className="form-group">
              <input
                type="email"
                name="email"
                className="form-control"
                placeholder="ایمیل"
              />
            </div>
            {/* end of email */}

            {/* subject */}
            <div className="form-group">
              <input
                type="text"
                name="subject"
                className="form-control"
                placeholder=" موضوع"
              />
            </div>
            {/* end of  subject */}

            {/* message */}
            <div className="form-group">
              <textarea
                name="message"
                className="form-control"
                placeholder="با عرض احترام"
                rows="10"
              />
            </div>
            {/* end of message */}

            {/* submit */}
            <div className="form-group mt-3">
              <input
                type="submit"
                value="ثبت"
                className="form-control  text-white"
                style={{ background: "#f57403" }}
              />
            </div>
          </form>
        </div>
      </div>
    </section>
  )
}

export default Contact
