import React from "react"
import CartItem from "./CartItem"
import { ProductConsumer } from "../../context"

const CartList = () => {
  return (
    <div className="continer-fluid">
      {/* row */}
      <div className="row">
        <div className="col">
          <ProductConsumer>
            {(value) => {
              const { cart, increment, decrement, removeItem } = value

              if (cart.length === 0) {
                return (
                  <h1 className="text tect-center my-6 p-3 justify-text d-flex text-align-center">
                    کارت  خرید شما خالی است
                  </h1>
                )
              }
              return (
                <>
                  {cart.map((item) => (
                    <CartItem
                      key={item.id}
                      cartItem={item}
                      increment={increment}
                      decrement={decrement}
                      removeItem={removeItem}
                    />
                  ))}
                </>
              )
            }}
          </ProductConsumer>
        </div>
      </div>
    </div>
  )
}

export default CartList
