import React from "react"
import { FaBars, FaCartPlus } from "react-icons/fa"
import { ProductConsumer } from "../context"
import styled from "styled-components"
import PersionNumber from '../components/PersianNumber'

const Navbar = () => {
  return (
    <ProductConsumer>
      {(value) => {
        const { cartItems, handleSidebar, handleCart } = value
        return (
          <NavWrapper>
            <div className="nav-center">
              <FaBars onClick={handleSidebar} className="nav-icon" />
              {/* <img
                src={logo}
                alt="store logo"
                style={{ width: "60px", hight: "60px" }}
              /> */}

              <div className="nav-cart">
                <FaCartPlus className="nav-icon" onClick={handleCart} />
                <div className="cart-items"><PersionNumber>{cartItems}</PersionNumber></div>
              </div>
            </div>
          </NavWrapper>
        )
      }}
    </ProductConsumer>
  )
}

const NavWrapper = styled.nav`
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100vw;
  padding: 1rem 1.5rem;
  background: #d9ffb3;
  /* #0e1c2e; */
  color: var(--mainWhite);
  z-index: 1;
  .nav-center {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 100%;
    /* max-width: 1170px; */
    /* margin-left: auto;
    margin-right: auto; */
    margin: 0 auto;
    /* border-bottom: 3px solid var(--primaryColor); */

    /* background: #0e1c2e;
    color: var(--mainWhite); */
  }

  .nav-icon {
    font-size: 2rem;;
    cursor: pointer;
    color: #4d9900
  }
  .nav-cart {
    position: relative;
  }

  .cart-items {
    position: absolute;
    top: -8px;
    right: -8px;
    font-size: 0.85rem;
    /* background: var(--primaryColor); */
    background:#ff334f;
    color: var(--mainWHite);
    border-radius: 50%;
    padding: 0 5px;
  }
`
export default Navbar
