import React from "react"
import { ProductConsumer } from "../context"
import styled from "styled-components"

const Footer = () => {
  return (
    <ProductConsumer>
      {(value) => {
        return (
          <FooterWrapper>
            <div className="container py-2 ">
              <div className="row">
                <div className="col-md-6 d-flex justify-content-end">
                  <p className="text-capitalize">
                    copyright &copy; veggies store all rights reserved{" "}
                  </p>
                </div>
                <div className="col-md-6 d-flex justify-content-end tex-aligh">
                  {value.socialIcons.map((item) => (
                    <a href={item.path} key={item.id}>
                      {item.icon}
                    </a>
                  ))}
                </div>
              </div>
            </div>
          </FooterWrapper>
        )
      }}
    </ProductConsumer>
  )
}

const FooterWrapper = styled.footer`
background:#4dbd28;
  color: var(--mainWhite);
  .icon {
    font-size: 1.5rem;
    color: var(--mainWhite);
    transition: var(--mainTransition);
    margin:7px
  }
  .icon:hover {
    color:#eeef20;
    cursor: pointer;
  }
`

export default Footer
